<template>
  <div id="app">
    <!--<img src="./assets/logo.png">-->
    <div class="wrap-banner">
      <div class="main-title">
        <h3>企业登陆</h3>
        <p class="login-text"> 用户名：</p>
        <el-input prefix-icon="el-icon-tickets" placeholder="请输入用户名" type="text" v-model="inputUsername" clearable>
        </el-input>
        <p class="login-text">密码：</p>
        <el-input prefix-icon="el-icon-view" placeholder="请输入密码" type="password" v-model="inputPwd" clearable>
        </el-input>
        <el-button type="primary" class="login-btn" @click="login" :loading="isLoading">登录</el-button>
      </div>
      <vue-particles
        color="#1234ff"
        :particleOpacity="0.7"
        linesColor="#ffffff"
        :particlesNumber="80"
        shapeType="circle"
        :particleSize="5"
        :linesWidth="2"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="150"
        :moveSpeed="3"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"
      >
      </vue-particles>
    </div>

  </div>
</template>

<script>
export default {
  data () {
    return {
      inputUsername: '',
      inputPwd: '',
      isLoading: false,
      testmasg: ''
    }
  },
  methods: {
    login: function (event) {
      if (this.inputUsername === '' || this.inputUsername.length <= 5) {
        this.$message.error('用户名格式不正确')
      } else if (this.inputPwd === '' || this.inputPwd.length <= 5) {
        this.$message.error('密码格式不正确')
      } else {
        this.isLoading = true
        this.axios.post('http://127.0.0.1:8000/login/', {
          'user_name': this.inputUsername,
          'user_pwd': this.inputPwd
        }).then(res => {
          this.isLoading = false
          console.log(res.data)
          this.testmasg = res.data
          if (res.data.code === 200) {
            this.$router.push({name: 'game', params: {userToken: res.data.data.userToken}})
            window.localStorage.setItem('userToken', res.data.data.userToken)
            this.$message({
              message: res.data.msg,
              type: 'success'
            })
          } else {
            this.$message.error(res.data.msg)
          }
        }).catch(res => {
          this.isLoading = false
          this.$message.error('网络错误')
        })
      }
    }
  }
}
</script>

<style lang="scss">

  *, :after, :before {
    box-sizing: border-box;
  }

  html, body {
    height: 100%;
  }

  body {
    margin: 0;
    background: #34393f;
  }

  #app {
    height: 100%;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }

  code[class*="language-"],
  pre[class*="language-"],
  pre,
  code {
    font-family: Menlo, Monaco, Andale Mono, Ubuntu Mono, monospace !important;
    font-size: .95em;
    @media (max-width: 767px) {
      font-size: .85em;
    }
  }

  pre.language-js, pre.language-html {
    max-width: 650px;
    margin: 1rem auto !important;
    background: #292929;
    border-radius: 3px;
    box-shadow: 2px 6px 17px rgba(0, 0, 0, 0.39);
  }

  .npm-code {
    background: #292929;
    border-radius: 3px;
    box-shadow: 2px 6px 17px rgba(0, 0, 0, 0.39);
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  h3 {
    font-weight: 100;
    font-size: 2rem;
  }

  .white {
    color: #ffffff;
  }

  .teko {
    font-family: 'Teko', sans-serif;
  }


  .particles-js {
    background-image: url("./assets/sky.jpg");
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .wrap-banner {
    position: relative;
    display: -webkit-flex;
    display: flex;
    height: 100%;

  }

  .main-section {
    max-width: 650px;
    margin: auto;
    padding: 0 1rem;
  }
  .main-title {
    color: #fff;
    z-index: 999;
    margin-left: 62%;
    margin-top: 14%;
    h2 {
      font-size: 7rem;
      font-family: 'Teko', sans-serif;
      text-shadow: 8px 8px #000000;
      text-transform: uppercase;
      margin: 0;
      @media (max-width: 767px) {
        font-size: 3rem;
      }
    }
    p.subtitle {
      font-size: 1.7rem;
      margin: 0;
      @media (max-width: 767px) {
        font-size: 1rem;
      }
    }
  }


  .section-title {
    margin: 2rem 0 0 0;
  }

  .footer {
    margin: 1rem 0;
  }


  .login-div {
    width: 300px;
    margin: auto auto;
  }

  .login-text {
    margin-top: 20px;
    text-align: left;
  }

  .login-btn {
    margin-top: 30px;
    width: 100%;
    z-index: 999;
  }
  h3{
    text-align: left;
  }

</style>



